@mixin light-theme-mode {}
@mixin dark-theme-mode {
  --bs-white:#132238;
  --bs-bg-dev-linear-gradient:linear-gradient(180deg, rgba(222, 228, 218, 0) 0%, rgba(222, 228, 218, 0.12) 100%);
  --bs-gray-900:#fff;
  --bs-gray-600:#fff;
  --bs-gray-700:#A5ACB5;
  --bs-bg-dev-services:#05182E;
  --bs-primary-50:#2B384C;
  --bs-gray-900-2:#2B384C;
  --bs-gray-800:#132238;
  --bs-border-one:#2B384C;
}

@mixin dark-mode {
  [data-theme="dark"] &,
  .dark-mode-texts & {
    @content;
  }
}
@mixin light-mode {
  [data-theme="dark"] &,
  .dark-mode-texts & {
    @content;
  }
}

